html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.swal2-popup {
  font-size: 2rem !important;
}

h1,
h2,
h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__description] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

body {
  background-color: #1a1b15;
  color: #fff;
}

.container {
  width: min(95%, 80rem);
  margin: 0 auto;
  padding: 50px 0;
}

.img-container {
  margin: 0 auto;
  text-align: center;
}

.img-container img {
  margin: 0 auto;
  max-width: 500px;
  max-height: 350px;
  object-fit: cover;
}

.img-container p {
  text-align: center;
}

.iframe-gmaps {
  margin: 0 auto;
  text-align: center;
}

.iframe-gmaps iframe {
  margin: 0 auto;
  max-width: 600px;
  height: 450px;
}

.footer {
  padding: 60px 0 40px;
  text-align: center;
}